.container-filter {
    width: 18%;
}
.Title-filter {
    text-align: center;
    margin: 30px 0px;
}
.ul-type {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin: 0;
}
li.li-type {
    margin: 10px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    width: 90%;
}

li.li-type a {
    color: #000;
    font-family: 'El Messiri', sans-serif;
    font-weight: 700;
}
.span-filter {
    font-family: cursive;
}
.type-filter {
    margin-bottom: 35px;
}

h4 {
    text-align: end;
    margin: 12px 12px;
    font-size: 19px;
    font-family: 'El Messiri', sans-serif;
}