.conainer-item {
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;
    width: 93%;
    justify-content: center;
}
.Cart-item {
    width: 186px;
    margin: 0 10px;
}
.Img-cart {
    width: 100%;
}

.Img-cart img {
    width: 182px;
    height: 265px;
}
.info-cart {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    font-family: 'El Messiri', sans-serif;
    
}
.info-cart h1 {
    font-size: 20px;
}

.info-cart h3 {
    color: #0000007d;
    font-size: 15px;

}

.Name-type {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 50%;
    
}
.price {
    width: 50%;
}
.like {
    position: relative;
    width: 100%;
    top: 30px;
    text-align: end;
    margin: 0px -10px;
    font-size: 24px;
    color: red;
}

@media (max-width:767px) {
  .container-filter {
    width: 43%;
    position: absolute;
    background: aliceblue;
    transform: translateX(335px);
    z-index: 1;
}  

    }
