.C-F-D {
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
    align-items: center;
    justify-content: center;
    background: #f5eeb138;
}
.F-Logo {
    width: 20%;
    text-align: center;
    margin: 25px 0;
}
.F-Logo h1 {
    font-family: 'El Messiri';
    margin-bottom: 31px;
}


ul.F-ul {
    display: flex;
    margin: 34px 0px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.F-list {
    width: 50%;
}
il.F-li {
    margin-bottom: 7px;
}
.F-louction {
    text-align: center;
    width: 22%;
}
.F-louction h1 {
    font-family: 'El Messiri';
    margin-bottom: 22px;
}
.F-louction a {
    border: 1px solid black;
    padding: 3px 49px;
}

@media (max-width:767px) {
    .C-F-D {
        display: flex;
        flex-direction: column-reverse;

    }
    .F-Logo {
        width: 100%;
    }
    .F-list {
        width: 100%;
    }
    .F-louction {
        width: 100%;
        margin-top: 24px;
    }

}


