.left-S {
    display: flex;
    width: 65%;
    flex-direction: row-reverse;
    align-items: center;
}
.H-img {
    width: 50%;
}
.Name-Pro-hero {
    width: 50%;
    text-align: end;
    margin: 0px 10px;
    font-family: 'El Messiri';
    
}
.Name-Pro-hero h1 {
    margin-bottom: 7px;
}
.Name-Pro-hero a {
    margin: 0px -3px;
    background: brown;
    color: #fff;
    padding: 6px;
    border-radius: 14px;
}
.sec-hero {
    width: 50%;
}

.sec-hero {
    width: 35%;
    text-align: center;
    margin: 334px 0px 0px 0px;
    font-family: 'El Messiri';
}
.sec-hero h1 {
    margin: 0px 0px 10px 0px;
}

.sec-hero a {
    background-color: #262829;
    padding: 4px 10px;
    border-radius: 14px;
    color: #fff;
}
@media (max-width:640px) {
    .Name-Pro-hero {
font-size: 11px;    
    }
}


@media (max-width:767px) {
    
.left-S {
    width: 100%;
    align-items: center;
  }
  .H-img img {
    width: 100%;
}
.sec-hero {
    width: 100%;
    margin: 0px 0px 0px 0px;
}
.sec-hero h1 {
    margin: 0px 0px 0px 0px;
    font-size: 23px;
}
.sec-hero a {
    font-size: 10px;
}
.H-img {
    width: 200%;
}
.Name-Pro-hero {
    width: 60%;

}
.Right-Title {
    margin: 0 0 27px;
}
}
    