.container-search {
    text-align: center;
    margin: 29px 0px 0px 0px;
    width: 87%;
}
input[type="search"] {
    width: 100%;
    height: 39px;
    border: none;
    border-bottom: 1px solid black;
    font-family: 'El Messiri', sans-serif;
    text-align: center;
    background: none;
}

form {
    display: flex;
    align-items: center;
    justify-content: center;
}
input[type="submit"] {
    display: none;
    height: 39px;
    width: 7%;
    font-size: 23px;
    border: none;
    border-bottom: 1px solid;
    font-family: 'El Messiri', sans-serif;

}