@import url('https://fonts.googleapis.com/css2?family=El+Messiri:wght@500&display=swap');
.Navbar {
    background-color: #27292a;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 75%;
    margin: 15px 171px;
    position: absolute;
    border-radius: 20px
}
.List {
    width: 90%;
}
ul {
    display: flex;
    margin: 10px 166px;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: center;
}
li {
    display: block;
    margin: 0 10px;

}
li a{
        color: #fff;
        font-family: 'El Messiri', sans-serif;
}
.Main-Icon {
    width: 10%;
    text-align: center;
    color: #fff;
}
i.fa-solid.fa-heart {
    margin: 0px 0px 0px 21px;
}
.icon-menu{
    display: none;
}
.list-Icon {
    display: none;
}
@media (max-width:767px) {
    .Navbar {
        width: 100%;
        background: none;
        margin: 10px 0px;
    }
    .List {
        width: 100%;
        position: absolute;
        top: 28px;
        display: none;
        background: #27292a;
        border-radius: 11px;
    }
    li {
        display: block;
        margin: 14px 10px;
    }
    .Main-Icon {
        width: 100%;
        display: flex;
        flex-direction: row-reverse;
    }
    .list-Icon {
        display: block;
        width: 94%;
        text-align: initial;
        font-size: 18px;
        color: #27292a;
        margin: 0px 0px 0px 15px;
    }

    
    i.fa-solid.fa-cart-shopping {
        margin: 0px 10px 0px 17px;
        color: #27292a;
        font-size: 18px;
    }
    i.fa-solid.fa-heart {
        font-size: 18px;
         color: #27292a;

    }
    ul {
        flex-direction: column;
    }
}