@import url('https://fonts.googleapis.com/css2?family=Cairo&display=swap');
.Container-HomePro {
    display: flex;
    flex-direction: row-reverse;
    width: 91%;
    margin: 0 auto;
    background: #eedecb61;
    height: 225px;
    margin-bottom: 99px;
    border-radius: 17px;
}
.imgHomepro {
    width: 35%;
    display: flex;
    align-items: center;
}
img.Img2 {
    width: 14%;
    position: absolute;
    transform: rotate(344deg);
}

.Img1 {
    width: 75%;
    z-index: 1;
}
.Title-Homepro {
    width: 65%;
    text-align: center;
}

.Title-Homepro h1 {
    margin-top: 21px;
    font-family: 'El Messiri';
}
.Btn-Homepro h3 {
    margin: 18px 5px;
    width: 211px;
    padding: 5px;
    border: 1px solid black;
    font-family: 'El Messiri';
    border-radius: 120px;
    font-size: small;
}
.Btn-Homepro {
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn-H-m a {
    border: 1px solid;
    padding: 3px 300px;
    border-radius: 20px;
}
@media (max-width:767px) {
    .Container-HomePro {
        display: flex;
        flex-direction: column;
        width: 95%;
        margin: 0 auto;
        background: #eedecb;
        margin-bottom: 99px;
        border-radius: 17px;
        align-items: center;
        height: unset;
    }
    .imgHomepro {
        width: 55%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    img.Img2 {
        width: 30%;
        transform: rotate(342deg);
    }
    .Title-Homepro {
        width: 100%;
        margin: 0px 0 60px 0;
    }
    .btn-H-m a {
        padding: 1% 39%;
    }
    .Btn-Homepro {
        width: 87%;
        margin: 0 auto;
    }
    .Btn-Homepro h3 {
       width: 30%;
    }
    }
    