.conaineBest {
    width: 90%;
    margin: 0 auto;
}
.Title-Best {
    font-size: 11px;
    font-family: 'El Messiri', sans-serif;
    display: flex;
    justify-content: flex-start;
    flex-direction: row-reverse;
    align-items: center;
}
.Title-Best h1 {
    margin: 0px 0px 0px 30px;
    background: aquamarine;
    padding: 0 12px;
    border-radius: 19px;
    font-size: 20px;
    cursor: pointer;

}
.Title-Best h2 {
    margin: 0px 0px 0px 65px;
    background: aquamarine;
    padding: 0 12px;
    border-radius: 19px;
    font-size: 20px;
    opacity: 0.5;
    cursor: pointer;
}
.Title-Best h2:hover {    opacity: 1;
}

.cart-best {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}
.best-item {
    width: 186px;
    margin: 0 20px;
}


@media (max-width:767px) {

    .Title-Best h2 {
        margin: 0px 0px 0px 0px;
    }
    

}