.containerShoes {
    width: 50%;
    display: flex;
    flex-direction: row-reverse;
    background: #ecdac4e0;
    border-radius: 21px;
    margin: 64px 21px;
}
.imgShoes {
    width: 40%;
}

.imgShoes img{
    width: 100%;
}
.TitleShoes {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.TitleShoes h1 {
    text-align: center;
    margin: 30px 0px 0px 0px;
    font-family: 'El Messiri', sans-serif;
}

.TitleShoes p {
    text-align: center;
    font-family: 'El Messiri', sans-serif;
    color: #000000a6;
    font-size: 18px;
    margin-bottom: 11px;
}
.TitleShoes a {
    background: aliceblue;
    padding: 2px 10px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
}

@media (max-width:767px) {
    

    .containerShoes {
        width: 90%;
        display: flex;
        flex-direction: column;
        margin: 25px 21px;
        align-items: center;
    }
    .TitleShoes {
        margin-bottom: 30px;
    }
}

